.search {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px;
    gap: 20px;
}

.search-general {
  margin-bottom: 10px;
  width: 100%;
  margin-left: 40px;
}

.search label {
   gap: 10px;
   font-size: x-large;
   font-weight: bolder;
}

.search input{
    padding: 18px;
    margin-left: 1%;
    margin-top: 5%;
}

.book-search {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    width: 100%;
    gap: 30px;
}

.book-search h2 {
  font-style: italic;
}

.nomeeautor {
  backdrop-filter: blur( 10px );
    -webkit-backdrop-filter: blur( 10px );
    background-color: rgba(128, 90, 70, 0.5);
    color: white;
    margin-bottom: 5px;
}

.book-search img {
  width: 200px;
  height: auto;
}

.book-search p{
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
    background-color: rgba(128, 90, 70, 0.7);
    padding: 20px;
    width: 1000px;
    backdrop-filter: blur( 12px );
    -webkit-backdrop-filter: blur( 12px );
    color: white;
    font-weight: 450;
    margin-right: 60px;
}

.book-search button {
    padding: 16px;
    font-size: 18px;
    border: none;
    color: white;
}

.buttons-search {
  display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 10px;
}

@media only screen and (max-width: 768px) {
  body {
    width: 100%;
}
  .book-search {
      display: flex;
      flex-direction: column;
      margin-left: -60px;
      gap: 5px;
  }
  .book-search img {
    width: 100px;
  }
  .book-search button{
    font-size: 15px;
    padding: 8px;
    margin-top: 5px;
  }
  .book-search p{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    line-height: 20px;
    width: 100%;
    color: white;
  }
  .book-search h1{
    font-size: 25px;
    margin-bottom: -10px
  }
  .book-search h2{
    font-size: 20px;
  }
  .details {
    font-size: 12px;
    color: white;
  }
  .search input {
    padding: 10px
  }
  .search label {
    font-size: large;
 }
}